import { Box, Button, Typography } from "@mui/material";
import ProfileSidebar from "./profileSidebar";
import { Routes, Route, useNavigate, useParams } from "react-router-dom";
import Universities from "./section/universities";
import Personal from "./section/personal";
import Documents from "./section/documents";
import TestScores from "./section/testScores";
import Preference from "./section/preference";
import Recommendations from "./section/recommendations";
import { ArrowBack } from "@mui/icons-material";
import { getStudentDetails } from "../../services";
import { useEffect, useState } from "react";
import AppliedUnivers from "./section/appliedUnivers";
import Education from "./section/education";
import WorkExperiences from "./section/workExperiences";
import Extracurricular from "./section/extracurricular";
import Family from "./section/family";
import { useAppDispatch } from "../../assets/hooks";
import { initRecommended } from "../../store/slices/recommendations";

interface ProfileProps {
  data?: {
    activity?: {
      applications?: any[]; 
      shortListed?: any[];
    };
    recommendations: any;
    education:any;
    workExperience:any;
    extraCurriculumActivities:any;
    preference:any;
    skills:string[];
    familyDetails:any
  };
}
export const DashboardRoutes = (props:ProfileProps) => {
  const { data } = props;
  return (
    <Routes>
      <Route path="applied" element={<AppliedUnivers data={data?.activity?.applications}/>}  />
      <Route path="universities" element={<Universities data={data?.activity?.shortListed}/>} />
      <Route path="personal" element={<Personal data={data}/>} />
      <Route path="documents" element={<Documents />} />
      <Route path="recommendation" element={<Recommendations  data={data?.recommendations?.data}/>} />
      <Route path="Education" element={<Education data={data?.education}/>} />
      <Route path="scores" element={<TestScores />} />
      <Route path="experience" element={<WorkExperiences  data={data?.workExperience}/>} />
      <Route path="activities" element={<Extracurricular data={data?.extraCurriculumActivities}/>} />
      <Route path="preferences" element={<Preference data={data?.preference} skills={data?.skills}/>} />
      <Route path="family" element={<Family data={data?.familyDetails} />} />
    </Routes>
  );
};

const ProfileLayout = () => {
  const navigate = useNavigate();
  const {id}:any = useParams();
  const [studentData, setStudentData] = useState<any>([]);
  const [loading, setLoading] = useState(false);
 const dispatch = useAppDispatch();

  useEffect(() => {
    const fetchData = async () => {
      const token = localStorage.getItem("_auth_token");
      if (!token) {
        localStorage.clear();
        navigate("/");
      }
      try {
        setLoading(true);
        const response = await getStudentDetails(id)
        if(response){
          setStudentData(response.data);
         dispatch(initRecommended(response.data?.recommendations))
        setLoading(false);

        }
      } catch (error) {
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  return (
    <Box>
      {/* Sidebar */}
      <Box
        sx={{
          background: "#3B3F76",
          padding: "8px 24px",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            gap: "15px",
          }}
        >
          <Button
            onClick={() => navigate("/dashboard")}
            sx={{
              color: "#FBE3C8",
              borderRadius: 0,
              textTransform: "none",
              fontSize: "1rem",
            }}
          >
            <ArrowBack sx={{ color: "#FBE3C8" }} />
            Previous page
          </Button>
        </Box>
      </Box>
      {loading ? <Typography textAlign="center">Loading ...</Typography> : 
      <Box sx={{ display: "flex" }}>
        <Box component="nav" sx={{background:"#FCF1EE"}}>
          <ProfileSidebar data={studentData}/>
        </Box>
        {/* Main content */}
        <Box
          component="main"
          sx={{ flexGrow: 1, p: 3, paddingRight: { lg: "5rem" } }}
        >
          <DashboardRoutes data={studentData}/>
        </Box>
      </Box>
}
    </Box>
  );
};

export default ProfileLayout;
