import React, { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import { Box, Button, Grid, Typography } from "@mui/material";
import { ArrowBack } from "@mui/icons-material";
import { Stage } from "./changeStage";

const Application = () => {
  const { id } = useParams();
  const [applicationData, setApplicationData] = useState<any>();
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchData = async () => {
      const token = localStorage.getItem("_auth_token");
      if (!token) {
        localStorage.clear();
        navigate("/");
      }
      try {
        setLoading(true);
        const { data } = await axios.get(
          `https://campusroot.com/api/v1/member/applications/${id}`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        setLoading(false);
        setApplicationData(data.data);
      } catch (error) {
        setLoading(false);
        console.error(
          "Error fetching student data:",
          // error.response.data.message
        );
      }
    };
    fetchData();
  }, []);

  return (
    <div className="container">
      {loading ? (
        <p>Loading...</p>
      ) : applicationData ? (
        <div>
          <Box
            sx={{
              background: "#3B3F76",
              padding: "8px 24px",
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <Box
              sx={{
                display: "flex",
                justifyContent: "space-between",
                gap: "15px",
              }}
            >
              <Button
                onClick={() => navigate("/dashboard")}
                sx={{
                  color: "#fff",
                  // borderBottom: activeTab === "leads" ? "2px solid #fff" : 0,
                  borderRadius: 0,
                  textTransform: "none",
                  fontSize: "1rem",
                }}
              >
                <ArrowBack sx={{ color: "#fff" }} />
                Previous page
              </Button>
            </Box>
          </Box>
          <h2 style={{ textAlign: "center" }}>Application Details</h2>
          <Grid container spacing={4} sx={{ p: 2 }}>
            <Grid item xs={3} sx={{ px: 2 }}>
              <Box sx={{ background: "#FCF1EE", p: 2 }}>
                <Box
                  sx={{
                    background: "#fff",
                    p: 1,
                    borderRadius: "10px",
                    mb: 1,
                    display: "flex",
                    gap: "10px",
                  }}
                >
                  <img
                    src={applicationData.user.displayPicSrc?applicationData.user.displayPicSrc:"https://res.cloudinary.com/dffdp7skh/image/upload/v1706879929/nvitahnrlhvmtcizpthx.png"}
                    alt="logo"
                    style={{ width: "2.5rem", height: "2.5rem" }}
                  />
                  <Box>
                    <Typography>
                      {applicationData.user.firstName}{" "}
                      {applicationData.user.lastName}
                    </Typography>
                    <Typography>{applicationData.user.email}</Typography>
                  </Box>
                </Box>
                <Box
                  sx={{
                    background: "#fff",
                    p: 1,
                    borderRadius: "10px",
                    mb: 1,
                    gap: "10px",
                    textAlign: "center",
                  }}
                >
                  <Link to={`/student/${applicationData.user._id}/personal`} target="_blank" style={{textDecoration:"none"}}>View profile</Link>
                </Box>
              </Box>
            </Grid>
            <Grid item xs={9} sx={{ p: 3 }}>
              <Box
                className="card"
                sx={{ boxShadow: 3, p: 2, borderRadius: "10px" }}
              >
                <Grid container>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      University
                    </Typography>
                    <Typography>{applicationData.university.name}</Typography>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      Location
                    </Typography>
                    <Typography>
                      {applicationData.university.location.city},{" "}
                      {applicationData.university.location.state},{" "}
                      {applicationData.university.location.country}
                    </Typography>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      Course
                    </Typography>
                    <Typography>
                      {applicationData.course.name} -{" "}
                      {applicationData.course.subDiscipline}
                    </Typography>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      Study Level
                    </Typography>
                    <Typography>{applicationData.course.studyLevel}</Typography>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      Duration
                    </Typography>
                    <Typography>
                      {applicationData.course.duration} months
                    </Typography>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      Established Year
                    </Typography>
                    <Typography>
                      {applicationData.university.establishedYear}
                    </Typography>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      Type
                    </Typography>
                    <Typography>{applicationData.university.type}</Typography>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px" }}>
                      Intake
                    </Typography>
                    <Typography>
                      {new Date(applicationData.intake).toLocaleDateString()}
                    </Typography>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      Status
                    </Typography>
                    <Typography>{applicationData.status}</Typography>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      Stage
                    </Typography>
                    <Stage stage={applicationData.stage} id={applicationData._id}/>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      Deadline
                    </Typography>
                    <Typography>
                      {applicationData.deadline
                        ? new Date(
                            applicationData.deadline
                          ).toLocaleDateString()
                        : "Not specified"}
                    </Typography>
                  </Grid>
                  <Grid item xs={6} sx={{ display: "flex", p: 1 }}>
                    <Typography sx={{ minWidth: 150, color: "#7C7A7A",fontSize:"14px"  }}>
                      Process Coordinator
                    </Typography>
                    <Typography>{applicationData?.processCoordinator?.firstName}</Typography>
                  </Grid>
                </Grid>
              </Box>
            </Grid>
          </Grid>
        </div>
      ) : (
        <p>No application data found</p>
      )}
    </div>
  );
};

export default Application;
