import { configureStore } from "@reduxjs/toolkit";
import  applicationsSlice  from "./slices/applicationSlice";
import popupSlice from "./slices/popupSlice";
import ProfileSlice from "./slices/profileInfo";
import recommendedCoursesSlice from "./slices/recommendations";
import studentsInfo from "./slices/studentsInfo";
import userAuthSlice from "./slices/userAuthSlice";

export const store = configureStore({
  reducer: {
    userAuthStatus: userAuthSlice,
    profileInfo: ProfileSlice,
    studentInfo: studentsInfo,
    applications:applicationsSlice,
    popup:popupSlice,
    recommendations:recommendedCoursesSlice,

  },
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
