import {
  Avatar,
  Box,
  Button,
  Collapse,
  List,
  ListItem,
  ListItemText,
  Paper,
  TextField,
  Typography,
} from "@mui/material";
import SendIcon from "@mui/icons-material/Send";
import Images from "../../assets";
import { maskInput } from "../../assets/library";
import { useEffect, useRef, useState } from "react";
import { switchStudentActivity } from "../../services";
import { useAppDispatch, useAppSelector } from "../../assets/hooks";
import { updateStudentProfile } from "../../store/slices/studentsInfo";

export const StudentName = (props: any) => {
  const handleClick = ()=>{
    if(props.type === "student"){
      window.open(`/student/${props.id}/personal`, "_blank")
    }else if(props.type === "application"){
      window.open(`/application/${props.id}`, "_blank")
    }
  }
  return (
    <div>
      <Box
        sx={{ display: "flex", gap: "20px", alignItems: "center" }}
        onClick={handleClick}
      >
        <Avatar
          src={
            props?.displayPicSrc
              ? props?.displayPicSrc
              : "https://res.cloudinary.com/dffdp7skh/image/upload/v1706879929/nvitahnrlhvmtcizpthx.png"
          }
          alt=""
          style={{ width: "2.5rem", height: "2.5rem", borderRadius: "50%" }}
        />
        {props?.name}
      </Box>
    </div>
  );
};
interface StageProps {
  stage: string;
  id: string;
}

export const Stage: React.FC<StageProps> = (props) => {
  const dispatch = useAppDispatch();
  const studentInfo = useAppSelector((state) => state.studentInfo);
  const listRef = useRef<HTMLUListElement | null>(null);
  const [selectedStage, setSelectedStage] = useState<string>(props.stage);
  const [inputValues, setInputValues] = useState<{ [key: string]: string }>({});
  const [expandedStage, setExpandedStage] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [isListOpen, setIsListOpen] = useState<boolean>(false);

  const stages = [
    "Fresh Lead",
    "Counselling",
    "Follow up 1",
    "Follow Up 2",
    "Final Shortlist",
    "Closed Lead",
    "Reopened",
  ];

  const handleStageClick = (stage: string) => {
    setExpandedStage(stage === expandedStage ? null : stage);
    setSelectedStage(stage);
  };

  const handleInputChange = (event: any, stage: string) => {
    setInputValues({ ...inputValues, [stage]: event.target.value });
  };

  const handleSubmit = async (stage: string) => {
    setLoading(true);
    try {
      const response = await switchStudentActivity({
        studentId: props.id,
        stage: selectedStage,
        note: inputValues[stage],
      });
      if (response) {
        console.log(response);
  
        let currentProfile = studentInfo.data.find(
          (profile) => profile._id === props.id
        );
        if (currentProfile) {
          dispatch(
            updateStudentProfile({ ...currentProfile, stage: selectedStage })
          );
          setIsListOpen(false);
          setLoading(false);
        }
      }
    }catch(err){}
   
  };

  const handleClickOutside = (event: MouseEvent) => {
    if (listRef.current && !listRef.current.contains(event.target as Node)) {
      setIsListOpen(false);
    }
  };

  useEffect(() => {
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);


  return (
    <Box sx={{ position: "relative", width: "100%" }}>
      <Button
        onClick={() => setIsListOpen(!isListOpen)}
        sx={{
          color: "#000",
          backgroundColor:
            props.stage === "Fresh Lead" ? "#FBE3C8" : "#F0F1F9 !important",
          textTransform: "none",
        }}
      >
        {props.stage}
      </Button>
      <Collapse in={isListOpen} timeout="auto" unmountOnExit >
        <Paper
          elevation={1}
          sx={{
            mt: 2,
            position: "absolute",
            zIndex: 1,
            top: 0,
            boxShadow: 2,
            width: "300px",
          }}
        >
          <List 
          ref={listRef}
          >
            {stages.map((stage) => (
              <div key={stage}>
                <ListItem button onClick={() => handleStageClick(stage)}  disabled={stage === selectedStage}>
                  <ListItemText primary={stage} />
                </ListItem>
                <Collapse
                  in={expandedStage === stage}
                  timeout="auto"
                  unmountOnExit
                >
                  <Box sx={{ pl: 4, pr: 4, pb: 2 }}>
                    <TextField
                      size="small"
                      fullWidth
                      value={inputValues[stage] || ""}
                      onChange={(event) => handleInputChange(event, stage)}
                      placeholder={`Enter value for ${stage}`}
                      sx={{ my: 1 }}
                    />
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={() => handleSubmit(stage)}
                      sx={{
                        backgroundColor: "#3B3F76",
                        color: "#fff",
                        fontWeight: "500",
                        fontSize: "13px",
                        width: "80px",
                        textTransform: "none",
                        borderRadius: "10px",
                        "&:hover": {
                          backgroundColor: "#3B3F76",
                          color: "#fff",
                        },
                      }}
                    >
                       {loading ? (
                              <img
                                src={Images.standardLoader}
                                alt="load"
                                style={{
                                  width: "20px",
                                  height: "20px",
                                  padding: "5px",
                                }}
                              />
                            ) : (
                              "Submit"
                            )}
                      
                    </Button>
                  </Box>
                </Collapse>
              </div>
            ))}
          </List>
        </Paper>
      </Collapse>
    </Box>
  );
};

export const Email = (props: any) => {
  const emailVerification = props?.verification?.find(
    (verificationItem: any) => verificationItem?.type === "email"
  );
  const maskedEmail = maskInput(props?.email, "email");
  return (
    <div>
      <Typography
        fontSize="14px"
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          gap: "20px",
        }}
      >
        {maskedEmail}
        <span
          style={{
            color: emailVerification?.status ? "green" : "",
            marginRight: "1rem",
          }}
        >
          {emailVerification?.status ? (
            <img src={Images.success} alt="" style={{ width: "1rem" }} />
          ) : (
            <SendIcon sx={{fontSize:"1rem"}}/>
          )}
        </span>
      </Typography>
    </div>
  );
};
export const Phone = (props: any) => {
  const phoneVerification = props?.verification?.find(
    (verificationItem: any) => verificationItem?.type === "phone"
  );
  const maskedEmail = maskInput(props.phone?.number, "phone");

  return (
    <div>
      <Typography
        fontSize="14px"
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          gap: "20px",
        }}
      >
        {`${props.phone?.countryCode ? props.phone?.countryCode : ""} ${
          props.phone?.number ? maskedEmail : "N/A"
        } `}
        <span
          style={{
            color: phoneVerification?.status ? "green" : "#B42318",
            marginLeft: "10px",
          }}
        >
          {phoneVerification?.status ? "Verified" : "Not verified"}
        </span>
      </Typography>
    </div>
  );
};
export const Recommendations = (props: any) => {
  return (
    <div>
      <Typography
        fontSize="14px"
        sx={{
          textAlign: "center",
          color: props?.data?.length > 0 ? "green" : "#B42318",
          padding: "10px",
        }}
      >
        {props?.data?.length > 0 ? "Yes" : "No"}
      </Typography>
    </div>
  );
};
