import { AxiosError } from "axios";
import { logOut } from "../assets/library";
import api from "./baseUrl";
import { memberEndPoint, loginEndPoint, studentDetailsEndPoint, teamMemberEndPoint, studentDetails, switchActivityEndPoint, switchStageEndPoint, approvalEndPoint, recommendUniversityEndPoint, searchUniversityEndPoint, allListingsEndPoint } from "./endPoints";

let token = localStorage.getItem("_auth_token");
const getHeaders = () => {
  token = localStorage.getItem("_auth_token");
  const headers = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    },
  };
  return headers;
};

let headers = getHeaders();

const handleApiError = (error: any) => {
    let errorMessage = "An error occurred";
  
    if (error && error instanceof AxiosError) {
      const errorResponse = error.response?.data;
      if (
        errorResponse?.message === "login again" ||
        errorResponse?.message === "jwt malformed"
      ) {
        errorMessage = errorResponse.message;
        localStorage.clear();
        logOut();
      } else {
        errorMessage = errorResponse?.message || errorMessage; // Use specific error message from API response if available
      }
    } else if (error && error instanceof Error) {
      errorMessage = error.message || errorMessage; // Use error message from JavaScript error object
    }
    return errorMessage;
  };
const authenticateLogin = async (values: object) => {
    try {
      const response = await api.post(loginEndPoint(), values, {
        withCredentials: true,
      });
      return response;
    } catch (err: any) {
      const errorResponse = handleApiError(err);
      throw errorResponse;
    }
  };
  const getMemberDetails = async () => {
  headers = getHeaders();
    try {
      const response = await api
        .get(memberEndPoint(), headers)
        .then((res) => res?.data);
      return response;
    }catch (err: any) {
      const errorResponse = handleApiError(err);
      throw errorResponse;
    }
  };
  const allstudentDetails = async (name:string,values:object) => {
  headers = getHeaders();
    try {
      const response = await api
        .post(studentDetails(name),values, headers)
        .then((res) => res?.data);
      return response;
    }catch (err: any) {
      const errorResponse = handleApiError(err);
      throw errorResponse;
    }
  };
  const getStudentDetails = async (studentId:string) => {
    try {
      const response = await api
        .get(studentDetailsEndPoint(studentId), headers)
        .then((res) => res?.data);
      return response;
    }catch (err: any) {
      const errorResponse = handleApiError(err);
      throw errorResponse;
    }
  };
  const switchStudentActivity = async (values:object) => {
    try {
      const response = await api.post(switchStageEndPoint(), values, headers);
      return response;
    }catch (err: any) {
      const errorResponse = handleApiError(err);
      throw errorResponse;
    }
  };
  const newApplicationApproval = async (values:object) => {
    try {
      const response = await api.post(approvalEndPoint(), values, headers);
      return response;
    }catch (err: any) {
      const errorResponse = handleApiError(err);
      throw errorResponse;
    }
  };

  const recommedUniversity = async (values:object) => {
    try {
      const response = await api.post(
        recommendUniversityEndPoint(),
        values,
        headers
      );
      return response;
    }catch (err: any) {
      const errorResponse = handleApiError(err);
      throw errorResponse;
    }
  };

  const getSearchUniversityDetails = async (search:string) => {
    try {
      const response = await api
        .get(searchUniversityEndPoint(search), headers)
        .then((res) => res?.data);
      return response;
    }catch (err: any) {
      const errorResponse = handleApiError(err);
      throw errorResponse;
    }
  };
  const getAlllistings = async (name: string, values: object) => {
    headers = getHeaders();
    try {
      const response = await api
        .post(allListingsEndPoint(name), values, headers)
        .then((res) => res?.data);
      return response;
    } catch (err: any) {
      const errorResponse = handleApiError(err);
      throw errorResponse;
    }
  };

  export {
    authenticateLogin,
    allstudentDetails,
    getMemberDetails,
    getStudentDetails,
    switchStudentActivity,
    newApplicationApproval,
    recommedUniversity,
    getSearchUniversityDetails,
    getAlllistings,
  };