
 import React, { useEffect, useRef, useState } from "react";
 import { Link, useNavigate, useParams } from "react-router-dom";
 import axios from "axios";
 import { Box, Button, Collapse, Grid, List, ListItem, ListItemText, Paper, TextField, Typography } from "@mui/material";
 import { ArrowBack } from "@mui/icons-material";
 import Images from "../../assets";
 import { newApplicationApproval } from "../../services";
 import { useAppDispatch } from "../../assets/hooks"; 

export const Stage= (props:{stage:string,id:string}) => {
    const dispatch = useAppDispatch();
    const listRef = useRef<HTMLUListElement | null>(null);
    const [selectedStage, setSelectedStage] = useState<string>(props.stage);
    const [inputValues, setInputValues] = useState<{ [key: string]: string }>({});
    const [expandedStage, setExpandedStage] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const [isListOpen, setIsListOpen] = useState<boolean>(false);
  
    const stages = [
        "Counsellor Approved",
        "Counsellor Disapproved",
      ]
      const processingStages = [
        "Request Received",
        "Document checklist",
        "Documents verification",
        "Application form draft",
        "Submission",
        "Pending Checklists",
        "Waiting for Decision",
        "Conditional Acceptance"
      ];
      const acceptedStages = [
        "Result Received",
        "Financial Documents",
        "I20 Request",
        "I20 Received",
      ];
  
    const handleStageClick = (stage: string) => {
      setExpandedStage(stage === expandedStage ? null : stage);
      setSelectedStage(stage);
    };
  
    const handleInputChange = (event: any, stage: string) => {
      setInputValues({ ...inputValues, [stage]: event.target.value });
    };
  
    const handleSubmit = async (stage: string) => {
      setLoading(true);
      let action;
      if(stage === "Counsellor Approved"){
          action = "approve"
      }else if (stage === "Counsellor Disapproved"){
        action = "disapprove"
      }
      try {
        const response = await newApplicationApproval({
          applicationId: props.id,
          action:action,
          justification: inputValues[stage],
        });
        if (response) {
          console.log(response);
    
        //   let currentProfile = studentInfo.data.find(
        //     (profile) => profile._id === props.id
        //   );
        //   if (currentProfile) {
        //     dispatch(
        //       updateStudentProfile({ ...currentProfile, stage: selectedStage })
        //     );
        //     setIsListOpen(false);
        //     setLoading(false);
        //   }
        }
      }catch(err){}
     
    };
  
    const handleClickOutside = (event: MouseEvent) => {
      if (listRef.current && !listRef.current.contains(event.target as Node)) {
        setIsListOpen(false);
      }
    };
  
    useEffect(() => {
      document.addEventListener("mousedown", handleClickOutside);
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
      };
    }, []);
  
  
    return (
      <Box sx={{ position: "relative", width: "100%" }}>
        <Button
          onClick={() => setIsListOpen(!isListOpen)}
          sx={{
            color: "#000",
            backgroundColor:
              props.stage === "Counsellor Approved" ? "#FBE3C8" : "#F0F1F9 !important",
            textTransform: "none",
          }}
        >
          {props.stage}
        </Button>
        <Collapse in={isListOpen} timeout="auto" unmountOnExit >
          <Paper
            elevation={1}
            sx={{
              mt: 2,
              position: "absolute",
              zIndex: 1,
              top: 0,
              boxShadow: 2,
              width: "300px",
            }}
          >
            <List
            ref={listRef}
            >
              {stages.map((stage) => (
                <div key={stage}>
                  <ListItem button onClick={() => handleStageClick(stage)}  disabled={stage === selectedStage}>
                    <ListItemText primary={stage} />
                  </ListItem>
                  <Collapse
                    in={expandedStage === stage}
                    timeout="auto"
                    unmountOnExit
                  >
                    <Box sx={{ pl: 4, pr: 4, pb: 2 }}>
                      <TextField
                        size="small"
                        fullWidth
                        value={inputValues[stage] || ""}
                        onChange={(event:any) => handleInputChange(event, stage)}
                        placeholder={`Enter value for ${stage}`}
                        sx={{ my: 1 }}
                      />
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={() => handleSubmit(stage)}
                        sx={{
                          backgroundColor: "#3B3F76",
                          color: "#fff",
                          fontWeight: "500",
                          fontSize: "13px",
                          width: "80px",
                          textTransform: "none",
                          borderRadius: "10px",
                          "&:hover": {
                            backgroundColor: "#3B3F76",
                            color: "#fff",
                          },
                        }}
                      >
                         {loading ? (
                                <img
                                  src={Images.standardLoader}
                                  alt="load"
                                  style={{
                                    width: "20px",
                                    height: "20px",
                                    padding: "5px",
                                  }}
                                />
                              ) : (
                                "Submit"
                              )}
                        
                      </Button>
                    </Box>
                  </Collapse>
                </div>
              ))}
            </List>
          </Paper>
        </Collapse>
      </Box>
    );
  };
