/* eslint-disable */
import React, { useState } from "react";
import {
  Box,
  Grid,
  Button,
  Typography,
  TextField,
  InputLabel,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import { Formik, Form, ErrorMessage } from "formik";
import * as Yup from "yup";
import Images from "../../assets";
import { authenticateLogin } from "../../services";
import { checkUser } from "../../assets/library";

const Login = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const loginSchema = Yup.object().shape({
    password: Yup.string()
      .min(3, "Too Short!")
      .max(50, "Too Long!")
      .required("Required"),
    email: Yup.string()
      .matches(
        /([a-z0-9]+)([_.-])?([a-zA-Z0-9]+)@([a-zA-Z0-9]+)([.])([a-z.]+)/g,
        "Enter valid Email"
      )
      .required("Enter Email ID")
      .max(40, "Email ID must be at most 40 characters"),
  });
  const submit = async (values: any) => {
    setLoading(true);
    const response = await authenticateLogin(values);
    console.log(response)
    if (response.data.success) {
      localStorage.setItem("_auth_token", response?.data?.data?.AccessToken);
      localStorage.setItem("role", response?.data?.data?.role);
      checkUser();
      navigate(
        response.data && response?.data?.data?.role === "counsellor"
          ? "/dashboard"
          : response?.data?.data?.role === "processCoordinator"
          ? "/dashboard"
          : "/"
      );
      setLoading(false);
    } else {
    }

    setLoading(false);
  };

  return (
    <>
      <Grid
        container
        sx={{
          display: "flex",
          // height: "70vh",
          overflowY: "hidden",
        }}
      >
        <Grid item sm={6} sx={{ display: { xs: "none", sm: "block" } }}>
          <img
            src={Images.counsellor}
            alt="counsellor-illustration"
            style={{ height: "99.5vh", width: "-webkit-fill-available" }}
          />
        </Grid>
        <Grid
          item
          xs={12}
          sm={6}
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            padding: "10%",
          }}
        >
          <Grid
            container
            sx={{ boxShadow: 3, padding: "8% 15%", borderRadius: "10px" }}
          >
            <Grid item container spacing={3} xs={12}>
              <Grid
                item
                xs={12}
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                <img
                  src={Images.oneWindowLogo}
                  alt="logo"
                  style={{ width: "40%" }}
                />
                <Typography
                  fontSize="26px"
                  gutterBottom
                  color="#FEB853"
                  fontWeight="600"
                >
                  Welcome back !
                </Typography>
              </Grid>
              <Formik
                initialValues={{ email: "", password: "" }}
                validationSchema={loginSchema}
                onSubmit={(values) => submit(values)}
              >
                {({ isSubmitting, values, handleChange }) => {
                  return (
                    <Form>
                      <Grid container>
                        <Grid item xs={12}>
                          <InputLabel sx={{ fontWeight: "600", color: "#000" }}>
                            Email Id
                          </InputLabel>
                          <TextField
                            fullWidth
                            variant="outlined"
                            className="form-input"
                            placeholder="Email"
                            name="email"
                            type="email"
                            size="small"
                            value={values.email}
                            onChange={handleChange}
                          />
                          <ErrorMessage name="email" component="div" />
                        </Grid>
                        <Grid item xs={12} sx={{ mt: 2 }}>
                          <InputLabel sx={{ fontWeight: "600", color: "#000" }}>
                            Password
                          </InputLabel>

                          <TextField
                            fullWidth
                            variant="outlined"
                            className="form-input"
                            placeholder="Password"
                            type="password"
                            size="small"
                            name="password"
                            value={values.password}
                            onChange={handleChange}
                          />
                          <ErrorMessage name="password" component="div" />
                        </Grid>
                        <Grid
                          item
                          xs={12}
                          sx={{ display: "flex", justifyContent: "end", mt: 1 }}
                        >
                          <Typography
                            // onClick={() => setLoginOrSignup("forgot")}
                            color="#7C7A7A"
                          >
                            Forgot password
                          </Typography>
                        </Grid>
                        <Grid item xs={12}>
                          <Button
                            variant="contained"
                            type="submit"
                            sx={{
                              backgroundColor: "#3B3F76",
                              color: "#fff",
                              fontWeight: "500",
                              fontSize: "16px",
                              width: "100%",
                              textTransform: "none",
                              borderRadius: "10px",
                              my: 5,
                              "&:hover": {
                                backgroundColor: "#3B3F76",
                                color: "#fff",
                              },
                            }}
                          >
                            {loading ? (
                              <img
                                src={Images.standardLoader}
                                alt="load"
                                style={{
                                  width: "20px",
                                  height: "20px",
                                  padding: "5px",
                                }}
                              />
                            ) : (
                              "Continue"
                            )}
                          </Button>
                        </Grid>
                      </Grid>
                    </Form>
                  );
                }}
              </Formik>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </>
  );
};

export default Login;
