import {
  Autocomplete,
  Box,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography,
} from "@mui/material";
import { ErrorMessage, Form, Formik } from "formik";
import React, { useState } from "react";
import Images from "../../assets";
import { useAppDispatch } from "../../assets/hooks";
import {
  getAlllistings,
  getSearchUniversityDetails,
  recommedUniversity,
} from "../../services";
import { closePopup } from "../../store/slices/popupSlice";
import { addRecommended } from "../../store/slices/recommendations";
import { Course, Event } from "../../types/types";

interface University {
  _id: string;
  name: string;
  logoSrc: string;
  community: string;
  courses: any;
}
const Recommendation = (props: any) => {
  const dispatch = useAppDispatch();
  const [options, setOptions] = useState<University[]>([]);
  const [selectedOption, setSelectedOption] = useState(null);
  const [univerId, setUniverId] = useState("");
  const [courses, setCourses] = useState<Course[]>([]);
  const [loading, setLoading] = useState(false);
  const [submiting, setSubmiting] = useState(false);

  const initialValues = {
    studentId: props.data,
    universityId: univerId,
    courseId: "",
    possibilityOfAdmit: "",
  };

  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 250,
      },
    },
  };

  const getUniversity = async (searchTerm: string) => {
    setLoading(true);
    const response = await getSearchUniversityDetails(searchTerm);
    if (response) {
      setOptions(response.data.universities);
    } else {
      // setError(response.data.message);
    }
    setLoading(false);
  };

  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "inputChange":
        if (event.data) {
          getUniversity(event.data);
        } else {
          setOptions([]);
        }
        break;
      case "optionChange":
        console.log(event.data);
        setSelectedOption(event.data);
        setUniverId(event.data?._id);
        if (event.data?._id) {
          const response = await getAlllistings("courses", {
            currentPage: 1,
            filterData: [{ type: "universityId", data: [event.data?._id] }],
          });
          if (response) {
            console.log(response);
            setCourses(response.data.list);
          }
        }

        break;
    }
  };

  const submit = async (values: any) => {
    setSubmiting(true);
    values.universityId = univerId;
    values.studentId = props.data;
    const response = await recommedUniversity(values);
    if (response) {
      dispatch(addRecommended(response.data.data));
      setSubmiting(false);
      dispatch(closePopup())
    }
  };
  
  return (
    <div>
      <Formik
        initialValues={initialValues}
        // validationSchema={profileSchema}
        onSubmit={(values: any) => submit(values)}
      >
        {({ touched, errors, values, handleChange }) => {
          return (
            <Form className="mt-5">
              <Autocomplete
                // name="universityId"
                options={options}
                onInputChange={(event, newValue) =>
                  eventHandler({ name: "inputChange", data: newValue })
                }
                getOptionLabel={(option) => option?.name}
                onChange={(event, newValue) =>
                  eventHandler({ name: "optionChange", data: newValue })
                }
                defaultValue={selectedOption}
                loading={loading}
                fullWidth
                size="small"
                // required
                sx={{
                  // mb: 2,
                  "& .MuiAutocomplete-listbox": {
                    maxHeight: "22vh !important",
                    height: "22vh !important",
                  },
                }}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    label="University"
                    variant="outlined"
                    sx={{
                      mb: 2,
                      "& .MuiAutocomplete-listbox": {
                        maxHeight: "22vh !important",
                        height: "22vh !important",
                      },
                    }}
                  />
                )}
              />
              <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel id="demo-simple-select-label">Courses</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  name="courseId"
                  value={values.courseId}
                  label="Courses"
                  required
                  size="small"
                  onChange={handleChange}
                  //   MenuProps={MenuProps}
                  MenuProps={{
                    PaperProps: {
                      //   onScroll:"",
                      style: {
                        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
                        width: 250,
                      },
                    },
                  }}
                >
                  <MenuItem value="" disabled>
                    Select type
                  </MenuItem>
                  {courses?.map((item, i) => (
                    <MenuItem
                      value={item._id}
                      key={i}
                      sx={{
                        whiteSpace: "normal",
                      }}
                    >
                      <div>
                        <Typography fontSize="15px" fontWeight="500">
                          {item.name}
                        </Typography>
                        <Typography fontSize="13px" fontWeight="300">
                          {item?.studyLevel},{item.subDiscipline}
                        </Typography>
                      </div>
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel id="demo-simple-select-label">
                  Possibility Of admit
                </InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  name="possibilityOfAdmit"
                  value={values.possibilityOfAdmit}
                  label="Possibility Of admit"
                  required
                  size="small"
                  onChange={handleChange}
                  MenuProps={MenuProps}
                >
                  <MenuItem value="">Select type</MenuItem>
                  {["Ambitious", "Moderate", "Safe"]?.map((item, i) => (
                    <MenuItem value={item} key={i}>
                      {item}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <ErrorMessage name="possibilityOfAdmit" component="div" />
              <Box sx={{ mt: 2, display: "flex", justifyContent: "center" }}>
                <Button
                  type="submit"
                  sx={{
                    backgroundColor: "#3B3F76",
                    color: "#fff",
                    fontWeight: "500",
                    fontSize: "13px",
                    width: "80px",
                    textTransform: "none",
                    borderRadius: "10px",
                    "&:hover": {
                      backgroundColor: "#3B3F76",
                      color: "#fff",
                    },
                  }}
                >
                  {submiting ? (
                    <Box
                      component="img"
                      src={Images.standardLoader}
                      alt="standardLoader"
                      sx={{ width: "1.5rem", height: "1.5rem" }}
                    />
                  ) : (
                    "Submit"
                  )}
                </Button>
              </Box>
            </Form>
          );
        }}
      </Formik>
    </div>
  );
};

export default Recommendation;
