import { Box, Button, } from "@mui/material";
import React, { useEffect, useState } from "react";
import { useAppDispatch, useAppSelector } from "../../assets/hooks";
import CalendarMonthIcon from "@mui/icons-material/CalendarMonth";
import {
  Email,
  Phone,
  Recommendations,
  Stage,
  StudentName,
} from "./applicationData";
import { allstudentDetails } from "../../services";
import { initStudentProfile } from "../../store/slices/studentsInfo";
import ReusableTable1 from "../../components/table";

const Counsellor = () => {
  const students = useAppSelector((state) => state.studentInfo);
  // const applications = useAppSelector((state) => state.applications);
  const role = localStorage.getItem("role");
  const dispatch = useAppDispatch();
  const [activeTab, setActiveTab] = useState<
    "students" | "leads" | "applications"
  >("students");
  const [applications, setApplications] = useState([]);

  const studentDetails = async () => {
    const response = await allstudentDetails("students", { filterData: [] });
    console.log(response);
    if (response) {
      dispatch(
        initStudentProfile({
          requestStatus: "initiated",
          responseStatus: "recieved",
          haveAnIssue: false,
          issue: "",
          data: response.data.list,
        })
      );
    }
  };

  const applicationDetails = async () => {
    const response = await allstudentDetails("applications", {
      filterData: [],
    });
    if (response.success) {
      setApplications(response.data.list);
      // dispatch(
      //               initStudentProfile({
      //                 requestStatus: "initiated",
      //                 responseStatus: "recieved",
      //                 haveAnIssue: false,
      //                 issue: "",
      //                 data: response.data.list,
      //               })
      //             );
    }
  };

  useEffect(() => {
    if (activeTab === "students") {
      if(role === "counsellor"){
        studentDetails();
      }
    } else if (activeTab === "applications") {
      applicationDetails();
    }
  }, [activeTab]);

  useEffect(() => {
    if (role === "counsellor") {
      setActiveTab("students");
    } else if (role === "processCoordinator") {
      setActiveTab("applications");
    } else {
      setActiveTab("leads");
    }
  }, [role]);

  return (
    <Box>
      <Box
        sx={{
          background: "#3B3F76",
          padding: "8px 24px",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Box
          sx={{ display: "flex", justifyContent: "space-between", gap: "15px" }}
        >
          {role === "counsellor" ? (
            <>
              <Button
                onClick={() => setActiveTab("students")}
                sx={{
                  color: "#fff",
                  borderBottom: activeTab === "students" ? "2px solid #fff" : 0,
                  borderRadius: 0,
                  textTransform: "none",
                  fontSize: "1rem",
                }}
              >
                Students
              </Button>
              <Button
                onClick={() => setActiveTab("applications")}
                sx={{
                  color: "#fff",
                  borderBottom:
                    activeTab === "applications" ? "2px solid #fff" : 0,
                  textTransform: "none",
                  borderRadius: 0,
                  fontSize: "1rem",
                }}
              >
                Applications
              </Button>
            </>
          ) : role === "processCoordinator" ? (
            <>
              <Button
                onClick={() => setActiveTab("applications")}
                sx={{
                  color: "#fff",
                  borderBottom:
                    activeTab === "applications" ? "2px solid #fff" : 0,
                  textTransform: "none",
                  borderRadius: 0,
                  fontSize: "1rem",
                }}
              >
                Applications
              </Button>
            </>
          ) : (
            <Button
              onClick={() => setActiveTab("leads")}
              sx={{
                color: "#fff",
                borderBottom: activeTab === "leads" ? "2px solid #fff" : 0,
                borderRadius: 0,
                textTransform: "none",
                fontSize: "1rem",
              }}
            >
              Leads
            </Button>
          )}
        </Box>
        <CalendarMonthIcon sx={{ color: "#fff" }} />
      </Box>
      <MainComponent
        activeTab={activeTab}
        students={students}
        applications={applications}
      />
    </Box>
  );
};

export default Counsellor;

interface MainComponentProps {
  activeTab: string;
  students: any;
  applications: any;
}
function formatDate(dateString: string, day: boolean): string {
  const options: Intl.DateTimeFormatOptions = day
    ? { year: "numeric", month: "long" }
    : { year: "numeric", month: "long", day: "numeric" };
  return new Date(dateString).toLocaleDateString(undefined, options);
}
const MainComponent: React.FC<MainComponentProps> = ({
  activeTab,
  students,
  applications,
}) => {
  const [columns, setColumns] = useState<any[]>([]);

  useEffect(() => {
    if (activeTab === "students") {
      setColumns(studentcolumns);
    } else if (activeTab === "applications") {
      setColumns(applicationRows);
    }
  }, [activeTab]);

  const mapStudentData = (students: any[]) => {
    return students.map(student => ({
      name: (
        <StudentName
          name={student.firstName + " " + student.lastName}
          displayPicSrc={student.displayPicSrc}
          id={student._id}
          type="student"
        />
      ),
      stage: <Stage stage={student.stage} id={student._id}/>,
      contact: (
        <>
          <Email email={student.email} verification={student.verification} />
          <Phone phone={student.phone} verification={student.verification} />
        </>
      ),
      recommendations: <Recommendations data={student.recommendations.data} />,
      country: (
        <ul style={{ padding: 0, paddingLeft: "20px" }}>
          {student.preference.country.length > 0
            ? student.preference.country.map((item: string, i: number) => (
                <li key={i}>{item}</li>
              ))
            : "N/A"}
        </ul>
      ),
      intake: student.preference.intake ? formatDate(student.preference.intake, true) : "N/A",
      degree: student.preference.degree || "N/A",
      budget:
        student.preference.budget.upper && student.preference.budget.lower
          ? `${student.preference.currency} ${student.preference.budget.upper.toLocaleString(
              "en-US"
            )} - ${student.preference.budget.lower.toLocaleString("en-US")}`
          : "N/A",
      language: student.preference.language || "N/A",
    }));
  };
  
  const mapApplicationData = (applications: any) => {
    return applications.map((application:any) => ({
      name: (
        <StudentName
          name={application.user.firstName + " " + application.user.lastName}
          displayPicSrc={application.user.displayPicSrc}
          id={application._id}
          type="application"
        />
      ),
      ProgramName:<>
      <div style={{ color: "#FEB853", fontWeight: 600 }}>
                  {application.course.unisName}
                </div>
                <div style={{ fontWeight: 500 }}>{application.course.name}</div></>,
      Deadline:formatDate(application.deadline, true),
      AppliedDate:formatDate(application.createdAt, true),
      Intake: application.intake ? formatDate(application.intake, true) : "N/A",
      Status:application.status,
      Stage: application.stage,
    }));
  };

  const studentcolumns = [
    { key: "name", name: "Name", minWidth: 150 },
    { key: "stage", name: "Stage", minWidth: 120 },
    { key: "contact", name: "Email / Phone", minWidth: 250 },
    { key: "recommendations", name: "Recommendations", minWidth: 150 },
    { key: "country", name: "Country", minWidth: 100 },
    { key: "intake", name: "Intake", minWidth: 100 },
    { key: "degree", name: "Degree", minWidth: 100 },
    { key: "budget", name: "Budget", minWidth: 150 },
    { key: "language", name: "Language", minWidth: 100 },
  ];
  const applicationRows = [
    { key: "name", name: "Name", minWidth: 150 },
    { key: "ProgramName", name: "Program Name", minWidth: 100 },
    { key: "Deadline", name: "Deadline", minWidth: 200 },
    { key: "AppliedDate", name: "Applied Date", minWidth: 200 },
    { key: "Intake", name: "Intake", minWidth: 100 },
    { key: "Status", name: "Status", minWidth: 100 },
    { key: "Stage", name: "Stage", minWidth: 100 },
  ];
  

  return (
    <div className="main-content" style={{ marginTop: "1rem" }}>
      {activeTab === "students" ? (
       <ReusableTable1 columns={columns} data={mapStudentData(students.data)} />
      ) : (
        <ReusableTable1 columns={columns} data={mapApplicationData(applications)} />

      )}
    </div>
  );
};
