import React, { useEffect, useRef } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Admin from "./pages/admin";
import Login from "./pages/authentication";
import Coordinator from "./pages/coordinator";
import Counsellor from "./pages/counsellor";
import Application from "./pages/Application/Application";
import Header from "./components/layout/header";
import { checkUser, getComponent } from "./assets/library";
import ProfileLayout, { DashboardRoutes } from "./pages/StudentProfile/profile";
import CustomModal from "./components/layout/customModal";
import { useAppSelector } from "./assets/hooks";

function App() {
  const isRequested = useRef(false);

  useEffect(() => {
    if (!isRequested.current) {
      checkUser();
      isRequested.current = true;
    }
  }, []);
  const popup = useAppSelector((state) => state.popup);
  const Popupcomponent: React.FC<{ data: any }> | null =
    popup.data.show && popup.data.data?.container?.name
      ? getComponent(popup?.data?.data?.container?.name)
      : null;
  return (
    <div>
      <BrowserRouter>
        <Header />
        <Routes>
          <Route path="/" element={<Login />} />
          <Route path="/student/:id/*" element={<ProfileLayout />}>
            <Route index element={<DashboardRoutes />} />
          </Route>
          <Route path="/application/:id" element={<Application />} />
          <Route path="/dashboard" element={<Counsellor />} />
          <Route path="/coordinator" element={<Coordinator />} />
          <Route path="/admin" element={<Admin />} />
        </Routes>
        <CustomModal
            open={popup.data.show}
            additionalData={{
              width: popup.data.data?.container?.dimensions?.width,
            }}
          >
            {Popupcomponent ? (
              <Popupcomponent data={popup.data.data?.container?.data} />
            ) : null}
          </CustomModal>
      </BrowserRouter>
    </div>
  );
}

export default App;
