import { Box, Button, TableCell, TableRow, Typography } from "@mui/material";
import { useState } from "react";
import { useParams } from "react-router-dom";
import Images from "../../../assets";
import { useAppDispatch, useAppSelector } from "../../../assets/hooks";
import ReusableTable from "../../../components/reusableTable";
import { setPopup } from "../../../store/slices/popupSlice";
import { Recommendation } from "../../../types/types";

const Recommendations = (props: any) => {
  const {id} = useParams();

  const recommendations = useAppSelector(state =>state.recommendations)
  console.log(recommendations);

  const [activeTab, setActiveTab] = useState<"Safe" | "Ambitious" | "Moderate">(
    "Safe"
  );
  const dispatch = useAppDispatch();
  const columns = [
    {
      name:"University / Course name",
      minWidth:170
    },   {
      name:"State",
      minWidth:170
    },   {
      name:"Country",
      minWidth:170
    },   {
      name:"Course level",
      minWidth:170
    },   {
      name:"Duration",
      minWidth:170
    },
  ]
  const ambitious: Recommendation[] = recommendations?.data?.filter(
    (item: any) => item?.possibilityOfAdmit === "Ambitious"
  );
  const safe: Recommendation[] = recommendations?.data?.filter(
    (item: any) => item?.possibilityOfAdmit === "Safe"
  );
  const moderate: Recommendation[] = recommendations?.data?.filter(
    (item: any) => item?.possibilityOfAdmit === "Moderate"
  );
  const StudentsData = (props: any) => {
    const { data } = props;
    return (
      <>
        {data?.length > 0 ? (
          data?.map((student: any) => (
            <TableRow
              key={student._id}
              sx={{
                "& .MuiTableCell-root": {
                  padding: "8px",
                  borderRightStyle: "solid",
                  borderRightColor: "gray",
                  borderWidth: "1px",
                },
              }}
              // onClick={() =>
              //   window.open(`/student/${student.profile._id}/universities`, "_blank")
              // }
            >
              <TableCell>
                <div
                  style={{
                    color: "#FEB853",
                    fontWeight: 600,
                    display: "flex",
                    justifyContent:"space-between"
                  }}
                >
                  <div style={{display: "flex",
                    gap: "10px",}}>
                  <img
                    src={student.university.logoSrc}
                    alt="logo"
                    style={{ width: "1.5rem", borderRadius: "5px" }}
                  />
                  {student.university.name}
                  </div>
                 
                  {student?.counsellorRecommended === true ?
                  <img src={Images.recommended} alt="logo"/> :"" }
                </div>
                <div style={{ fontWeight: 500 }}>{student.course.name}</div>
              </TableCell>
              <TableCell>
                <div style={{ fontWeight: 500 }}>
                  {student.university.location.state}
                </div>
              </TableCell>
              <TableCell>
                <div style={{ fontWeight: 500 }}>
                  {student.university.location.country}
                </div>
              </TableCell>
              <TableCell>
                <div style={{ fontWeight: 500 }}>
                  {student.course.studyLevel}
                </div>
              </TableCell>

              <TableCell>
                <div style={{ fontWeight: 500 }}>
                  {student.course.duration} months
                </div>
              </TableCell>
            </TableRow>
          ))
        ) : (
          <TableRow sx={{ "&:last-child td, &:last-child th": { border: 0 } }}>
            <TableCell colSpan={6}>
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Typography align="center">No data available</Typography>
              </div>
            </TableCell>
          </TableRow>
        )}
      </>
    );
  };
  return (
    <div>
        <Box sx={{display:"flex",justifyContent:"space-between",mb:2}}>
        <Box sx={{display:"flex",gap:"10px"}}>
        <Button
          onClick={() => setActiveTab("Safe")}
          sx={{
            color: activeTab === "Safe" ? "#000" :"#7C7A7A",
            background: activeTab === "Safe" ? "#FEB853" : "",
            borderRadius: 0,
            textTransform: "none",
            fontSize: "1rem",
            "&:hover": {
              background: "#FEB853",
            },
          }}
        >
          Safe
        </Button>
        <Button
          sx={{
            color: activeTab === "Moderate" ? "#000" :"#7C7A7A",
            background: activeTab === "Moderate" ? "#FEB853" : "",
            borderRadius: 0,
            textTransform: "none",
            fontSize: "1rem",
            "&:hover": {
              background: "#FEB853",
            },
          }}
          onClick={() => setActiveTab("Moderate")}
        >
          Moderate
        </Button>
        <Button
          sx={{
            color: activeTab === "Ambitious" ? "#000" :"#7C7A7A",
            background: activeTab === "Ambitious" ? "#FEB853" : "",
            borderRadius: 0,
            textTransform: "none",
            fontSize: "1rem",
            "&:hover": {
              background: "#FEB853",
            },
          }}
          onClick={() => setActiveTab("Ambitious")}
        >
          Ambitious
        </Button>
      </Box>
      <Box sx={{
        display:"flex",justifyContent:"center",mt:2
      }}>
        <Button 
         sx={{
          color:"#fff",
          background: "#3B3F76",
          borderRadius: "5px",
          textTransform: "none",
          fontSize: "0.8rem",
          "&:hover": {
            background:"#3B3F76",
          },
        }}
        onClick={()=>
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "addRecommendation",
                data:id,
                dimensions: {
                  width: "500px",
                },
              },
              type: "custom",
            },
          })
        )}>Recommend course</Button>
      </Box>
        </Box>

      
      {activeTab === "Safe" ? (
        <ReusableTable
          columns={columns}
          data={<StudentsData data={safe} />}
          rowOnClick={undefined}
        />
      ) : activeTab === "Ambitious" ? (
        <ReusableTable
          columns={columns}
          data={<StudentsData data={ambitious} />}
          rowOnClick={undefined}
        />
      ) : (
        <ReusableTable
          columns={columns}
          data={<StudentsData data={moderate} />}
          rowOnClick={undefined}
        />
      )}
     
    </div>
  );
};

export default Recommendations;
